<template>
  <div>
    <b-container>
      <b-row>
        <b-col offset-md="2">
          <b-tabs content-class="mt-1">
            <b-tab title="Daily" active>
              <b-table class="my-daily-table" :items="items" :fields="fields">
                <template v-slot:cell(actions)="row">
                  <b-button size="sm" class="mr-1" @click="info(row.item, $event.target)">
                    EDIT
                  </b-button>
                </template>
              </b-table>
            </b-tab>
            <b-tab title="Monthly">
              <b-table
                class="my-monthly-table"
                :items="monthly"
                :fields="monthlyFields"
              />
            </b-tab>
          </b-tabs>
        </b-col>
      </b-row>
      <b-modal v-model="showModal" hide-footer size="lg" title="Edit">
        <b-container>  
          <b-row>
            <b-col md="6">
              <label for="">Date</label>
            </b-col>
            <b-col md="3">
              <label for="">Time Started</label>
            </b-col>
            <b-col md="3">
              <label for="">Time Finished</label>
            </b-col>
          </b-row>
          <b-row>
            <b-col md="6">
              <b-calendar block v-model="newDate" />
            </b-col>
            <b-col md="3">
              <b-time v-model="newTimeStarted" show-seconds locale="en" />
            </b-col>
            <b-col md="3">
              <b-time v-model="newTimeFinished" show-seconds locale="en" />
            </b-col>
          </b-row>
          <b-row class="mt-5 modal-footer">
            <b-col md="2" offset-md="10">
              <b-button @click="update">Update</b-button>
            </b-col>
          </b-row>
        </b-container>
      </b-modal>
    </b-container>
  </div>
</template>
<script>
import moment from "moment";
export default {
  data() {
    return {
      details: [],
      items: [],
      monthly: [],
      fields: [
        {
          key: "created_date",
          thStyle: {
            display: "none"
          }
        },
        {
          key: "timeElapse",
          thStyle: {
            display: "none"
          }
        },
        {
          key: "category",
          thStyle: {
            display: "none"
          }
        },
        {
          key: "detail",
          thStyle: {
            display: "none"
          }
        },
        { 
          key: 'actions', 
          thStyle: {
            display: "none"
          }
        }
      ],
      monthlyFields: [
        {
          key: "month",
          thStyle: {
            display: "none"
          }
        },
        {
          key: "category",
          thStyle: {
            display: "none"
          }
        },
        {
          key: "value",
          thStyle: {
            display: "none"
          }
        }
      ],
      showModal: false,
      currentIndex: null,
      newDate: '',
      newTimeStarted: '',
      newTimeFinished: ''
    };
  },
  mounted() {
    let today = moment(this.created_date).format("YYYY-MM-DD");
    if (JSON.parse(localStorage.getItem("Details"))) {
      this.details = JSON.parse(localStorage.getItem("Details"));

      this.items = JSON.parse(localStorage.getItem("Details"))
        .filter(item => item.created_date == today)
        .map((data, index) => {
          return data
        })

      this.checkMonthly(today);
    }
  },
  methods: {
    info(item, button) {
      let index = this.details.findIndex(obj => {
        return obj.timeElapse == item.timeElapse && obj.category == item.category && obj.currentMonth == item.currentMonth && obj.detail == item.detail && obj.timeStarted == item.timeStarted && obj.timeFinished == item.timeFinished
      })
      this.currentIndex = index
      this.newDate = this.details[index].created_date
      this.newTimeStarted = this.details[index].timeStarted
      this.newTimeFinished = this.details[index].timeFinished
      this.showModal = !this.showModal
    },
    update() {
      let today = moment(this.created_date).format("YYYY-MM-DD");
      let startTime = moment(this.newTimeStarted, "HH:mm:ss")
      let finishTime = moment(this.newTimeFinished, "HH:mm:ss")
      let duration = moment.duration(finishTime.diff(startTime))
      let checkTime = moment(startTime).isAfter(finishTime)
      if (checkTime) {
        alert('Time Started must be before Time Finished')
      } else {
        this.details[this.currentIndex] = {
          'created_date': this.newDate,
          'current_month': moment(this.newDate).format('YYYY-MM'),
          'timeElapse': `${startTime._i} - ${finishTime._i}`,
          'timeStarted': startTime._i,
          'timeFinished': finishTime._i,
          'totalSeconds': duration.asSeconds(),
          'detail': this.details[this.currentIndex].detail,
          'category': this.details[this.currentIndex].category
        }
        localStorage.setItem('Details', JSON.stringify(this.details))
        this.items = this.details.filter(item => item.created_date == today)
          .map((data, index) => {
            return data
          })
        
        this.checkMonthly(today)
        this.showModal = false
      }
    },
    checkMonthly(today) {
      this.monthly = []
      let result = this.details.reduce((acc, curr) => {
        if (!acc[curr.current_month]) {
          acc[curr.current_month] = {};
        }
        if (!acc[curr.current_month][curr.category]) {
          acc[curr.current_month][curr.category] = [];
        }

        acc[curr.current_month][curr.category].push(curr.totalSeconds);

        return acc;
      }, []);

      for (let monthly in result) {
        for (let category in result[monthly]) {
          this.monthly.push({
            category: category,
            month: monthly,
            value: (
              result[monthly][category].reduce((acc, curr) => {
                return acc + curr;
              }, 0) / 3600
            ).toFixed(3)
          });
        }
      }
    }
  }
};
</script>
