}

table.my-daily-table > tbody {
  max-height: 500px;
}

table.my-monthly-table > thead,
table.my-monthly-table > tbody,
table.my-monthly-table > tfoot {
  display: block;
  width: 50%;
  overflow-y: scroll;
}

table.my-monthly-table > tbody {
  max-height: 500px;
}

.modal-backdrop:not(.show):not(.fade) {
    opacity: .5;
}
</style>
